<template>
    <div>
        <h1 class="divided">钓鱼</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <div v-if="dropId">
                <h4>钓鱼</h4>
                <item-drop-list :dropId="dropId" :dropsItem="result.drops"></item-drop-list>
            </div>
            注：
            <p>每次自动钓鱼需花{{result.list._MaxTime}}秒</p>
            <p>钓鱼成功时，获得经验值{{result.list._GetSkillPointSuccess}},失败时获得经验值{{result.list._GetSkillPointFailure}}</p>

            <h4>鱼竿大全</h4>
            <responsive-card-list :count="result.rod.length">
                <responsive-card-list-entry 
                    v-for="(e, key) in result.rod" 
                    :key="key">
                    <item-card 
                        :itemId="e.id"
                        :itemStub="e"
                        :count="1"
                    />
                </responsive-card-list-entry>
            </responsive-card-list>
        </template>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import Loader from "@/components/util/Loader";
Vue.component("load-indicator", Loader);
import ItemCard from "@/old/items/ItemCard";
Vue.component('item-card', ItemCard);
import ItemDropList from "@/components/item/ItemDropList.vue";
import ResponsiveCardList from "@/components/util/ResponsiveCardList.vue";
import ResponsiveCardListEntry from "@/components/util/ResponsiveCardListEntry.vue";
export default {
    created(){
        this.fetchData();
    },
    components: {
        GoBack,
        ItemIconTooltip,
        ItemDropList,
        ResponsiveCardList,
        ResponsiveCardListEntry
    },
    data(){
        return{
            result:null,
            noLink:false,
            loading:true,
            dropId:0,
        }
    },
    methods:{
        fetchData(){
            if(GameInfo.FarmFishCache){
                this.result = GameInfo.FarmFishCache;
                this.dropId = parseInt(GameInfo.FarmFishCache.list._SuccessDrapID);
                this.loading = false;
            }else{
            this.loading = true;
            GameInfo.FarmFish(this.$store.state.regionCode,
                    (res) => {
                        this.dropId = parseInt(res.list._SuccessDrapID);
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        },
    }
}
</script>
<style lang="less" scoped>
.Title {
    display: flex;
    flex-direction: row;

    .titleitem {
        flex: 0 0 auto;
    }
}
</style>

