<template>
    <div>
        <h1 class="divided">生活技能</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
                <table border="1">
                    <tr>
                        <td></td>
                        <td style="height:2rem;" v-for="(v,k) in result.skill" :key="k">
                            <div class="item-icon">
                                    <span class="item" :style="`background:url(${cover}) -${getIcon(v._SecondarySkillIcon).x}px -${getIcon(v._SecondarySkillIcon).y}px`"></span>
                            </div>
                            <div>{{v.skillName}}</div>
                        </td>
                    </tr>
                    <tr v-for="(v,k) in result.level" :key="k">
                        <td style="min-width:2rem;">{{k}}级</td>
                        <td v-for="(vv,kk) in v" :key="kk">
                            <p>需达到经验值：<span style="color:#91c32e;">{{vv._SecondarySkillExp}}</span> </p>
                             <span style="white-space: pre-wrap;">{{vv.levelName}}</span>
                        </td>
                    </tr>
                </table>
        </template>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import Loader from "@/components/util/Loader";
import { ApiHttpClient } from "@/globals";
Vue.component("load-indicator", Loader);
export default {
    created(){
        this.fetchData();
    },
    components: {
        GoBack,
    },
    data(){
        return{
            result:null,
            loading:true,
            cover:`${ApiHttpClient.defaults.resourceURL}/dds/lifeskillicon.png`,
        }
    },
    methods:{
        fetchData(){
            if(GameInfo.FarmSkillCache){
                this.result = GameInfo.FarmSkillCache;
                this.loading = false;
            }else{
            GameInfo.FarmSkill(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        },
        getIcon(pageIdx){
            let row = Math.floor(pageIdx / 10);
            let column = pageIdx % 10;
            const UNIT_SIZE = 52;

            let ret = {
                x: Math.max(UNIT_SIZE * column, 0),
                y: UNIT_SIZE * row,
            };
            return ret;
        },
    }
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.item-icon {
    width: 52px;
    height: 52px;
    position: relative;
    display: inline-block;

    .item {
        position: absolute;
        top: 1px;
        .left(4px);
        .right(0);
        bottom: 0;
    }
}
</style>
