<template>
    <div>
        <h1 class="divided">料理</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <table border="1">
                <thead>
                    <th>食谱</th>
                    <th>需要熟练度</th>
                    <th>名称</th>
                    <th>料理</th>
                    <th>需要材料</th>
                    <th>获得经验值</th>
                </thead>
                    <tr v-for="(v,k) in result.list" :key="k">
                        <td>
                            <item-icon-tooltip
                                :itemId="v.RowId"
                                :itemStub="v.recipe"
                                :count="1"
                                :noLink="noLink">
                            </item-icon-tooltip>
                        </td>
                        <td>{{parseInt(v._SecondarySkillClass) | farmLevelName}}</td>
                        <td>{{v.category}}</td>
                        <td>
                            <item-icon-tooltip
                                :itemId="v._MakeItemID" 
                                :itemStub="v.makeItem.item"
                                :count="v.makeItem.count"
                                :noLink="noLink">
                            </item-icon-tooltip>
                        </td>
                        <td>
                            <div class="Title">
                                <div v-for="(vv,kk) in v.material" :key="kk" class="titleitem">
                                    <item-icon-tooltip
                                        :itemId="vv.itemId" 
                                        :itemStub="vv.item"
                                        :count="vv.count"
                                        :noLink="noLink">
                                    </item-icon-tooltip>
                                </div>
                            </div>
                        </td>
                        <td>{{v._SuccessUpExp}}点</td>
                    </tr>
            </table>
            
            <pager :pageSize="pageSize" v-model="pageNo" @on-jump="jump" style="float:right;"></pager>

            <p>
                注：每次制作需耗时3秒
            </p>
            <p>制作数量为随机，上表显示最多制作数量</p>
        </template>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import Loader from "@/components/util/Loader";
import pager from '@/components/util/Pager';
Vue.component("load-indicator", Loader);
export default {
    created(){
        this.fetchData();
    },
    components: {
        GoBack,
        ItemIconTooltip,
        pager
    },
    data(){
        return{
            result:{
                list:null,
                count:null,
            },
            noLink:false,
            loading:true,
            pageSize: 30,
            pageNo: 1,
            cacheArray:[],
        }
    },
    methods:{
        jump(id){
            this.pageNo = id;
            this.fetchData();
        },
        fetchData(){
            if(this.cacheArray[this.pageNo]){
                this.result = this.cacheArray[this.pageNo];
                this.loading = false;
            }else{
            this.loading = true;
            GameInfo.FarmCook(this.pageNo,this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.pageSize = Math.floor(res.count/15) + 1;
                        this.loading = false;
                        this.cacheArray[this.pageNo] = res;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        },
    }
}
</script>
<style lang="less" scoped>
.Title {
    display: flex;
    flex-direction: row;

    .titleitem {
        flex: 0 0 auto;
    }
}
</style>