<template>
    <div>
        <h1 class="divided">耕种</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
                <table border="1">
                    <thead>
                        <th>种子</th>
                        <th>需要熟练度</th>
                        <th>栽培时间</th>
                        <th>浇水</th>
                        <th>经验值</th>
                        <!-- <th>overlap</th> -->
                        <th style="min-width:92px;">收成</th>
                    </thead>
                        <tr v-for="(v,k) in result.list" :key="k">
                            <td>
                                <item-icon-tooltip
                                    :itemId="v.item.id"
                                    :itemStub="v.item"
                                    :count="1"
                                    :noLink="noLink">
                                </item-icon-tooltip>
                            </td>
                            <td>{{parseInt(v._SecondarySkillClass) | farmLevelName}}</td>
                            <td>{{v._CultivateMaxTime | formattingTime}}</td>
                            <td>
                                最大浇水量:{{v._MaxWater}}
                                每秒消耗水量{{v._ConsumeWater}}
                            </td>
                            <td>
                                播种{{v._SkillpointSeed}}点，每次浇水{{v._SkillpointWater}}点
                            </td>
                            <!-- <td>{{v._Overlap}}</td> -->
                            <td><a href="javascript:void(0)" @click="checkHarvest(v._HarvestDropID,v.item.name.name)">查看收成</a></td>
                        </tr>
                </table>
                <template v-if="popup && dropId" >
                    <dialog-frame :title="title" :visible="popup" @closePopup="closeHarvest">
                        <item-drop-list :dropId="dropId"></item-drop-list>
                    </dialog-frame>
                </template>
                注：
                <p>收割农产品需携带镰刀。可从农场商店购买</p>
                <p>上述耕种均可用成长促进剂 <item-icon-tooltip :itemId="result.harvest.id" :itemStub="result.harvest" :count="1" :noLink="noLink"></item-icon-tooltip>
                </p>
                <h3>农场商店</h3>
                <shop-browser :shopId="shopId"  :useClick="useClick" />
        </template>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import Loader from "@/components/util/Loader";
import DialogFrame from "@/components/util/DialogFrame";
import ShopBrowser from '@/components/item/shop/ShopBrowser.vue';
import ItemCard from "@/old/items/ItemCard";
Vue.component('item-card', ItemCard);
import ItemDropList from "@/components/item/ItemDropList.vue";
Vue.component("load-indicator", Loader);
export default {
    created(){
        this.fetchData();
    },
    components: {
        GoBack,
        ItemIconTooltip,
        ShopBrowser,
        DialogFrame,
        ItemDropList
    },
    data(){
        return{
            result:{
                list:null,
                count:null,
            },
            noLink:false,
            loading:true,
            popup:false,
            dropId: 0,
            title:null,
            shopId:45001,
            useClick: true
        }
    },
    methods:{
        fetchData(){
            if(GameInfo.FarmCultivateCache){
                this.result = GameInfo.FarmCultivateCache;
                this.loading = false;
            }else{
            this.loading = true;
            GameInfo.FarmCultivate(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        },
        checkHarvest(dropId,name){
            this.popup = true;
            this.title = name + ' 收成如下';
            this.dropId = parseInt(dropId);
        },
        closeHarvest(){
            this.popup = false;
        }
    }
}
</script>

